@import url(https://fonts.googleapis.com/css2?family=Delius&display=swap);
.main-head {
  background-color: white;
  position: fixed;
  width: 100%;
  min-height: 30px;
  z-index: 999;
  top:0;
}
.wrapper{
  max-width: 1000px;
  margin: 0px auto;
}
.navigation ul{
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  margin: 0px auto;
}
.navigation li{
  list-style: none;
  position: relative; 
}
.navigation li img{
  width:90px;
  margin:10px 50px 0px 0px;
}
.navigation li a {
  text-decoration: none;
  padding: 15px;
  color:black;
}
.navigation li a:after {   
  position: absolute;
  background-color: #dce7e8;
  height: 2px;
  bottom: 0;
  content: "";
  transition: width 0.5s ease 0s, left 0.5s ease 0s;
  width: 0;
  left: 50%;
}
.navigation a:hover{
  color:  black;
}
.navigation li a:hover:after{ 
  width: 100%; 
  left: 0; 
}
